import React, { useEffect, useRef } from 'react';
import { getLocale } from './utils';


function Modal({title, body, onHide}) {

  const containerRef = useRef(null);

  useEffect(() => {
    const $ = window.$;
    window.containerRef = containerRef;
    $(containerRef.current)
      .modal()
      .on('hidden.bs.modal', onHide);
  });

  return (
    <div ref={containerRef} className="modal fade" id="" tabIndex="-1" role="dialog" aria-labelledby="Success Message" aria-hidden="true">
      <div className="modal-dialog modal-dialog-centered" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">{title}</h5>
            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div className="modal-body" dangerouslySetInnerHTML={{__html: body}} />
          <div className="modal-footer">
            <button type="button" className="btn btn-primary" data-dismiss="modal">{getLocale('close')}</button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Modal;
