import React, { Component }  from 'react';
import { connect }  from 'react-redux';
import * as operations from '../state/operations';
import * as selectors from '../state/selectors';
import { noop } from './utils';
import * as questions from './questions';


const cacheTypeComponents = {};

const addGenericProperties = (QuestionTypeComponent, type) => {
  const genericProperties = {
    isValid: (answer) => Boolean(answer),
    nextIfValid: false,
    dontEdit: false,
    parseCompleted: ($div) => $div.data('completed'),
    parseID: ($div) => $div.attr('id').slice(2),
    parseProperties: ($div) => {},
    parseTitle: ($div) => $div.find('label[for]').text(),
    parseValue: ($div) => {
      const $inputs = $('input, select, textarea', $div);
      if ($inputs.length > 1){
        const inputsType = $inputs.attr('type');
        const $checked = $inputs.filter(':checked');
        if (inputsType==='checkbox'){
          var values = [];
          $checked.each(function(index, element){
            values.push($(element).val());
          });
          return values;
        }else if (inputsType==='radio'){
          if (type===13){
            return this._getValueType13();
          }else if (type===14){
            return this._getValueType14();
          }else{
            return $checked.val();
          }
        }
      }else{
        return $inputs.val();
      }
    },
    type
  };
  Object.entries(genericProperties).forEach(([name, value], index) => {
    if (QuestionTypeComponent[name] === undefined) {
      QuestionTypeComponent[name] = value;
    }
  });
  return QuestionTypeComponent;
};

export const getQuestionTypeComponent = (type) => {
  if (cacheTypeComponents[type]) return cacheTypeComponents[type];
  const QuestionTypeComponent = questions['QuestionType'+type];
  addGenericProperties(QuestionTypeComponent, type);
  cacheTypeComponents[type] = QuestionTypeComponent;
  return QuestionTypeComponent;
};

export default @connect(
  (state, props) => ({
  }),
  operations
)
class Question extends Component {

  constructor(props) {
    super(props);
    this.QuestionTypeComponent = getQuestionTypeComponent(props.type);
  }

  handleAnswerChange = (value) => {
    const { id, updateQuestion } = this.props;
    const valid = this.QuestionTypeComponent.isValid(value);
    updateQuestion({
      id,
      answer: value,
      valid,
    });
  }

  render() {
    const { completed, id, lastQuestion, readOnly, title, type, error, waiting } = this.props;
    const htmlId = `id-${id}`;
    const disabled = readOnly || waiting;
    return (
      <div id={`q${id}`} className={`cepa-question form-group question-type-${ type }${ completed ? ' completed' : ''}${ error ? ' error' : ''}${ lastQuestion ? ' last-question' : ''}`}>
        {
          title && (
            <label
              className="cepa-question__label"
              disabled={disabled}
              htmlFor={htmlId}
            >
            { title }</label>
          )
        }
        <this.QuestionTypeComponent
          {...this.props}
          disabled={disabled}
          htmlId={htmlId}
          onChange={this.handleAnswerChange}
        />
        {
          error && (<div className="error-text">{ error }</div>)
        }
      </div>
    );
  }

}
