import React from 'react';
import { getIsoDateString } from '../utils';
import QuestionType1 from './QuestionType1';


function QuestionType2(props) {

  function handleChange(value) {
    props.onChange(getIsoDateString(value));
  }

  return (
    <QuestionType1
      {...props}
      onChange={handleChange}
      inputType="date"
    />
  );
}

QuestionType2.getValidValueForTest = (data) => {
  const inputValue = {
    vt: '10/31/1976',
    in: '10/31/1976',
    id: '10/31/1976',
    ph: '10/31/1976',
    pk: '10/31/1976',
    en: '10/31/1976',
    es: '31/10/1976',
    pt: '31/10/1976',
  }[window.CEPA.lang];
  return getIsoDateString(inputValue);
};

QuestionType2.parseValue = ($div) => {
  const inputValue = $('input[type="date"]', $div).val();
  return getIsoDateString(inputValue);
};

export default QuestionType2;
