import React, { useEffect, useRef, useState }  from 'react';
import { getLocale } from '../utils';
import Portal from '../../utils/Portal';
import YouTube from 'react-youtube';


function QuestionType15({ answer, id, card, properties: { youtubeId, width=1920, height=1080 }, onChange=noop, selected, selectCard }) {
  const [hidden, setHidden] = useState(false);
  const [playing, setPlaying] = useState(false);
  const videoWrapper = useRef(null);
  const videoElement = useRef(null);

  const prevSelectedRef = React.useRef(false);
  React.useEffect(() => {
    if (prevSelectedRef.current && !selected && playing) {
      videoElement.current.internalPlayer.pauseVideo();
    }
    prevSelectedRef.current = selected; // cache current value for next render
  }, [selected, playing]);

  useEffect(() => {
    window.addEventListener('scroll', () => {
      setHidden(playing && videoWrapper.current.getBoundingClientRect().bottom < videoWrapper.current.offsetHeight / 2);
    });
  });

  const handleOnStateChange = (event) => {
    switch(event.data) {
      case 1:
        setPlaying(true);
        break;
      default:
        setPlaying(false);
    }
  };

  const handleBackToVideo = () => {
    selectCard(card);
    window.CEPA.scrollToElement(videoWrapper.current);
  };

  const handleVideoEnded = () => {
    onChange(true);
  };


  const options = {
    height: '100%',
    width: '100%',
    playerVars: {
      // 'autoplay': 0,
      // 'controls': 1,
      // 'suggestedQuality': 'small',
      // 'mute': 0,
      'enablejsapi': 1,
      'modestbranding': 1,
      'playsinline': 1,
      'showinfo': 0,
      'rel': 0,
      // 'origin': 'http://localhost:3000/'
    }
  };

  // player.playVideo():Void
  // player.pauseVideo():Void
  // player.stopVideo():Void
  // player.seekTo(seconds:Number, allowSeekAhead:Boolean):Void

  // player.mute():Void
  // player.unMute():Void
  // player.isMuted():Boolean
  // player.setVolume(volume:Number):Void
  // player.getVolume():Number

  // player.setSize(width:Number, height:Number):Object
  // player.getPlaybackRate():Number
  // player.setPlaybackRate(suggestedRate:Number):Void
  // player.getAvailablePlaybackRates():Array

  // player.getPlayerState():Number
  //   -1  unstarted
  //   0  ended
  //   1  playing
  //   2  paused
  //   3  buffering
  //   5  video cued

  // player.getVideoLoadedFraction():Float
  // player.getCurrentTime():Number
  // player.getDuration():Number

  // player.destroy():Void

  // onReady={func}                    // defaults -> noop
  // onStateChange={func}              // defaults -> noop
  // onPlaybackQualityChange={func}    // defaults -> noop
  // onPlaybackRateChange={func}       // defaults -> noop
  // onError={func}                    // defaults -> noop

  // onPlay={func}                     // defaults -> noop
  // onPause={func}                    // defaults -> noop
  // onEnd={func}                      // defaults -> noop

  return (
    <div className={`video-player${hidden ? ' hidden' : ''}`}>
      <div
        className='ratio-grid'
        ref={videoWrapper}
      >
        <div>
          <svg xmlns="http://www.w3.org/2000/svg" viewBox={`0 0 ${width} ${height}`} />
        </div>
        <YouTube
          ref={videoElement}
          videoId={youtubeId}
          opts={options}
          // onReady={this.handleOnReady}
          onStateChange={handleOnStateChange}
          // onError={this.handleOnError}
          onEnd={handleVideoEnded}
          // className=''
          // containerClassName=''
        />
      </div>

      <Portal id="back-to-video-portal">
        <div className={`back-to-video-wrapper${hidden ? ' video-player-hidden' : ''}`}>
          <button
            className="back-to-video"
            onClick={handleBackToVideo}
          >
            {getLocale('backToVideo')}
            <i className="icon-arrows-green" />
          </button>
        </div>
      </Portal>
    </div>
  );
}

QuestionType15.dontEdit = true;

QuestionType15.getValidValueForTest = (data) => {
  return true;
};

QuestionType15.parseProperties = ($div) => {
  const youtubeId = $div.data('youtube-id');
  const width = $div.data('width');
  const height = $div.data('height');
  return {
    youtubeId,
    width,
    height,
  };
};

QuestionType15.nextIfValid = true;

export default QuestionType15;
