import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import './utils/polyfills';
import configureStore from './state/store';
import App from './components/App';
import { loadScript } from './components/utils';
import { getDataFromHTML, prepareInitialState } from './utils/parser';
import * as serviceWorker from './serviceWorker';
import './index.scss';


const $ = window.$;
const $assessment = $('#assessment');

const dataFromHTML = getDataFromHTML($assessment);

window.CEPA = window.CEPA || {};
if (window.CEPA.locales === undefined) {
  loadScript('https://www.bytelockers.com/cepa/static/js/locales.js');
}
window.CEPA.lang = $('html').attr('lang') || 'es';
window.CEPA.postURL = $assessment.attr('action');
window.CEPA.module = $assessment.data('module');
window.CEPA.successRedirect = $assessment.data('success-redirect');
window.CEPA.finalText = window.CEPA.finalText || $('#final-text').html();
window.CEPA.scrollToElement = function (element) {
  var headerHeight = window.matchMedia('(min-width: 576px)').matches ? 62 : 40;
  var backToVideoHeight = 46;
  $([document.documentElement, document.body]).animate({
    scrollTop: $(element).offset().top - headerHeight - backToVideoHeight
  }, 500);
};

const rootElement = document.getElementById('root');
const initialState = prepareInitialState(window.CEPA.initialState || dataFromHTML);
const store = configureStore(initialState);

rootElement && ReactDOM.render(
  <Provider store={store}>
    <App />
  </Provider>,
  rootElement
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

$(function(jQuery){

  const $thumbsSlider = $('.thumbs-slider');
  $thumbsSlider.slick && $thumbsSlider.slick({
    slidesToShow: 3,
    slidesToScroll: 3,
    infinite: false,
    prevArrow: '<div class="slick-prev">Previous</div>',
    nextArrow: '<div class="slick-next">Next</div>',
    responsive: [
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: false,
        }
      },
      {
        breakpoint: 576,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          infinite: false,
        }
      },
    ],
  });
  const selectedCard = $('.cepa-card.selected')[0];
  if (selectedCard) setTimeout(function(){
    window.CEPA.scrollToElement(selectedCard);
  });
});
