export const arrayDiff = (a, b) => {
  return a.filter(itemFor => b.indexOf(itemFor) < 0);
};

export const flattenList = (list) => {
  return list.reduce(
    (accumulator, itemFor) => [
      ...accumulator, ...itemFor
    ],
    []
  );
};

export const flattenDict = (dict) => {
  return Object.values(dict).reduce(
    (accumulator, itemFor) => ({
      ...accumulator, ...itemFor,
    }),
    {}
  );
};

export const noop = () => null;

export const sameArray = (a, b) => {
  return a.length === b.length && arrayDiff(a, b).length === 0;
};

export const getLocale = (key) => {
  try {
    const localKey = window.CEPA.locales[key];
    return localKey[window.CEPA.lang] || localKey.en || key;
  } catch (error) {
    return key;
  }
};

export const loadScript = (url, callback=noop) => {
  var script = window.document.createElement('script');
  script.type = 'text/javascript';
  if (script.readyState){  //IE
    script.onreadystatechange = function() {
      if (script.readyState == 'loaded' || script.readyState == 'complete') {
        script.onreadystatechange = null;
        callback();
      }
    };
  } else {  //Others
    script.onload = function(){
      callback();
    };
  }
  script.src = url;
  document.getElementsByTagName('head')[0].appendChild(script);
};


export const validateEmail = (email) => {
  const validEmailRe = /^(?:[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*|"(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21\x23-\x5b\x5d-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])*")@(?:(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?|\[(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?|[a-z0-9-]*[a-z0-9]:(?:[\x01-\x08\x0b\x0c\x0e-\x1f\x21-\x5a\x53-\x7f]|\\[\x01-\x09\x0b\x0c\x0e-\x7f])+)\])$/;
  return validEmailRe.test(email);
};


export const getParameterByName = (name, url = window.location.href) => {
    name = name.replace(/[\[\]]/g, '\\$&');
    var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)'),
        results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, ' '));
};


export const getIsoDateString = (plainValue) => {
  let day, month, year, isoDateString;
  const humanDateFormatParts = plainValue.split('/');
  if (humanDateFormatParts.length === 3){
    year = humanDateFormatParts[2];
    if (window.CEPA.lang === 'es' || window.CEPA.lang === 'pt'){
      day = humanDateFormatParts[0];
      month = humanDateFormatParts[1];
    } else {
      month = humanDateFormatParts[0];
      day = humanDateFormatParts[1];
    }
    isoDateString = year + '-' + month + '-' + day;
  }else{
    isoDateString = plainValue;
  }
  return isoDateString;
};
