export const isCardSelected = (card, state) => {
	return card.index == state.currentCardIndex;
};

export const getCardFromIndex = (cardIndex, state) => {
  return state.cards.find(card => card.index === cardIndex);
};

export const getCompletedCards = (state, direction=true) => {
  return state.cards.filter(({completed}) => !direction ^ completed);
};

export const getPendingCards = (state, direction=true) => {  // cards that are not complete and not waiting
  return state.cards.filter(({completed, waiting}) => direction ^ (completed || waiting));
};

export const getQuestionFromId = (questionId, state) => {
  state.cards.forEach((cardFor) => {
    const cardMatchingQuestion = cardFor.questions.find(questionFor => questionFor.id === questionId);
    if (cardMatchingQuestion) return cardMatchingQuestion;
  });
};

export const getTotalCards = (state) => {
  return state.cards.length;
};

export const getCurrentCardIndex = (state) => {
  return state.currentCardIndex;
};

export const getCurrentCard = (state) => {
  return getCardFromIndex(getCurrentCardIndex(state), state);
};

export const getQuestionCardFromId = (questionId, state) => {
  const question = getQuestionFromId(questionId, state);
  return getCardFromIndex(question.card, state);
};
