import React, { Fragment, useRef, useEffect } from 'react';


function QuestionType1({ answer, disabled, htmlId, index, inputType, onChange=noop, selected }) {
  const inputEl = useRef(null);

  useEffect(() => {
    if (index===0 && selected && !disabled)
    inputEl.current.focus();
  }, [disabled, index, selected]);

  return (
    <Fragment>
      <input
        type={inputType || "text"}
        id={htmlId}
        className="form-control"
        readOnly={disabled}
        ref={inputEl}
        name={htmlId}
        value={answer}
        onChange={(event) => onChange(event.currentTarget.value)}
      />
    </Fragment>
  );
}

QuestionType1.getValidValueForTest = (data) => {
  return 'test short text';
};

export default QuestionType1;
