import React from 'react';


export default function Choice({ answer, data: { label, id }, disabled, onChange, choiceType='radio' }) {

  const htmlId = `id-${id}`;
  const checked = (choiceType === 'checkbox') ? answer.includes(id) : answer===id;

  return (
    <div className={`cepa-choice custom-control custom-${choiceType}`}>
      <input
      className="custom-control-input"
      type={choiceType}
      id={htmlId}
      disabled={disabled}
      name={id}
      value={id}
      checked={checked}
      onChange={onChange}
    />
    <label
      className="custom-control-label"
      htmlFor={htmlId}
      disabled={disabled}
    >
      {label}
    </label>
    </div>
  );

}
