import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import * as operations from '../state/operations';
import { flattenDict } from './utils';
import Deck from './Deck';


function App({data}) {
  const dispatch = useDispatch();

  function keydownHandler({ key, metaKey, shiftKey }) {
    if (metaKey && shiftKey && (key === 'u' || key === 'e')) dispatch(operations.answerCurrentCardForTest());
  }

  useEffect(() => {
    window.addEventListener('keydown', keydownHandler);
    return () => {
      window.removeEventListener('keydown', keydownHandler);
    };
  }, []);


  return (
    <div className="cepa-app">
      <Deck />
    </div>
  );
}

export default App;
