import React, { Fragment, useRef } from 'react';
import { getLocale } from '../utils';
import QuestionType1 from './QuestionType1';


function QuestionType6(props) {
  const { answer, htmlId, onChange, disabled } = props;

  const inputEl = useRef(null);
  const selectEl = useRef(null);

  let quantity = '';
  let units = window.CEPA.lang === 'en' ? 'm' : 'k';
  if (answer) {
    quantity = answer.slice(0,-1);
    units = answer.slice(-1);
  }

  function handleChange(event) {
    onChange(inputEl.current.value + selectEl.current.value);
  }

  return (
    <Fragment>
      <input
        type="number"
        id={htmlId}
        className="form-control"
        readOnly={disabled}
        ref={inputEl}
        name={htmlId}
        value={quantity}
        onChange={handleChange}
      />
      <select
        disabled={disabled}
        className="custom-select"
        onChange={handleChange}
        ref={selectEl}
        value={units}
      >
        <option value="k">{getLocale('kmts')}</option>
        <option value="m">{getLocale('miles')}</option>
      </select>
    </Fragment>
  );
}

QuestionType6.isValid = (answer) => {
  if (!answer || answer.length < 2) return false;
  const quantity = answer.slice(0,-1);
  const units = answer.slice(-1);
  return (units === 'k' || units === 'm')  && !isNaN(quantity);
};

QuestionType6.getValidValueForTest = (data) => {
  return '100k';
};

export default QuestionType6;
