import React, { Fragment, Component } from 'react';
import { connect, useDispatch, useSelector, useState } from 'react-redux';
import { noop, getLocale, getParameterByName } from './utils';
import * as operations from '../state/operations';
import * as selectors from '../state/selectors';
import axios from 'axios';
import Question, { getQuestionTypeComponent } from './Question';


export function Buttons({ index, canEdit, completed, editing, onSubmit, onToggleEdit, readOnly, selected, valid, waiting }) {
  const totalCards = useSelector(state => selectors.getTotalCards(state));
  const dispatch = useDispatch();
  const prevVisible = index > 0;
  const nextVisible = index < totalCards - 1;
  const submitButton = (
    <button
      className="btn btn-primary"
      disabled={!valid || waiting}
      onClick={onSubmit}
    >
      { waiting ? getLocale('submitting') : getLocale('submit') }
    </button>
  );
  const editButton = (
    <button
      className="btn btn-primary-outline"
      onClick={ onToggleEdit }
    >
      { editing ? getLocale('cancel') : getLocale('edit') }
    </button>
  );

  return (
      <div className="card__buttons">

        <div className="card__buttons__actions">
          { canEdit && completed && editButton }
          { canEdit && !readOnly && submitButton }
        </div>

        <div className="card__buttons__arrows">
          { prevVisible && (
            <button
              disabled={!selected}
              className="arrow-prev"
              onClick={() => dispatch(operations.prevCard())}
            >
              {getLocale('previous')}
            </button>
          ) }
          { nextVisible && (
            <button
              disabled={!selected}
              className="arrow-next"
              onClick={() => dispatch(operations.nextCard())}
            >
              {getLocale('next')}
            </button>
          ) }
        </div>
      </div>
  );
}

export default @connect(
  null,
  operations,
  null,
  {
    forwardRef: true,
  },
)
class Card extends Component {

  state = {
    canEdit: true,
    editing: false,
    readyToSubmit: false,
  }

  constructor(props) {
    super(props);
    this.cardRef = React.createRef();
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const { completed: cardCompleted, questions, index } = nextProps;
    const { editing } = prevState;
    const nextState = {};

    const readyToSubmit = questions.every(({type, completed, valid}, index) => {
      const nextIfValid = getQuestionTypeComponent(type).nextIfValid;
      const readyToSubmitFor = ((!cardCompleted || !completed || editing) && valid && nextIfValid);
      return readyToSubmitFor;
    }
    );
    if (readyToSubmit !== prevState.readyToSubmit) nextState.readyToSubmit = readyToSubmit;

    const dontEdit = questions.every(({type}) => getQuestionTypeComponent(type).dontEdit);
    const canEdit = !dontEdit;
    if (canEdit !== prevState.canEdit) nextState.canEdit = canEdit;
    return nextState;
  }

  componentDidUpdate(prevProps, prevState) {
    const { editing, readyToSubmit } = this.state;
    const { editing: wasEditing, readyToSubmit: wasReadyToSubmit } = prevState;
    const { selected } = this.props;
    const { selected: wasSelected } = prevProps;

    if (selected && selected !== wasSelected) {
      window.CEPA.scrollToElement(this.cardRef.current);
    }
    if (readyToSubmit && !wasReadyToSubmit && !(editing && !wasEditing)) {
      this.handleSubmit();
    }
  }

  getPayload = () => {
    const { questions } = this.props;
    const answers = questions.map(
      ({ id, answer }) => ({ id, answer })
    );
    return {
      module: window.CEPA.module,
      answers,
    };
  }

  fetchData = (url, data) => {
    const { index } = this.props;
    const errorByParam = getParameterByName('error') !== null;
    const developByParam = getParameterByName('develop') !== null;
    const testError = errorByParam || (process.env.REACT_APP_TEST_ERROR === 'true');
    const production = !developByParam && (process.env.NODE_ENV === 'production');
    const productionConfig = {
      url,
      method: 'post',
      data: encodeURIComponent(JSON.stringify(data)),
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
      }
    };
    const developConfig = {
      url: `_ajax/server_response_${index+1}${testError?'_error':''}.json`,
      method: 'get',
      params: data,
    };
    const response = axios(production ? productionConfig : developConfig);
    return response;
  }

  setWaiting(direction) {
    const { index, updateCard } = this.props;
    updateCard({ index, waiting: direction });
  }

  sendAnswers = async () => {
    const { valid } = this.props;
    if (!valid) return;
    this.setWaiting(true);
    const postURL = window.CEPA.postURL;
    var data = this.getPayload();
    const response = await this.fetchData(postURL, data);
    try {
      this.successResponse(response.data);
    } catch (error) {
      console.error('error', error);
    }
    this.setWaiting(false);
  }

  handleSubmit = () => {
    const { gotoFirstUncompletedCard, index, valid } = this.props;
    if (!valid) return;
    this.setState({
      editing: false
    });
    this.sendAnswers();
    gotoFirstUncompletedCard(index);
  }

  handleToggleEdit = () => {
    this.setState(({editing}) => ({editing: !editing}));
  }

  completeQuestion(id, error) {
    const { nextCard, updateQuestionCallback, completed } = this.props;
    const questionCompleted = !Boolean(error);
    updateQuestionCallback({
      id,
      completed: questionCompleted,
      error,
    }).then(()=>{
    });
  }

  successResponse(data) {
    const { setFinalText } = this.props;
    if (data.valids) {
      data.valids.map((id) => this.completeQuestion(id));
    }
    if (data.errors) {
      data.errors.map(({id, error}) => this.completeQuestion(id, error));
    }
    if (data.final_text) {
      setFinalText(data.final_text);
    }
  }

  getUpdateAnswersData = (id, data, replace=false) => {
    const { answers } = this.state;
    const newAnswers = replace ?
      { ...answers, [id]: data }
        :
      Object.entries(answers).reduce((accumulator, [idFor, prevData]) => {
        accumulator[idFor] = (id !== idFor) ? prevData : { ...prevData, ...data };
        return accumulator;
      }, {});
    return newAnswers;
  }

  render() {
    const { className, index, questions, title, completed, selected, valid, waiting } = this.props;
    const { canEdit, editing } = this.state;
    const readOnly = !selected || (completed && !editing) || waiting;

    const questionsComponents = questions.map((dataFor, questionIndex) => {
      return (
        <Question
          index={questionIndex}
          key={questionIndex}
          lastQuestion={questionIndex === questions.length-1}
          readOnly={readOnly}
          selected={selected}
          waiting={waiting}
          {...dataFor}
        />
      );
    });

    return (
      <div className={`row cepa-card ${ className }${ selected ? ' selected' : ''}${ completed ? ' completed' : ''}${ valid ? ' valid' : ''}${ waiting ? ' waiting' : ''}`} ref={this.cardRef}>
        <div className="card col px-0 mb-4 shadow-sm">
          <div className="card-body">
            { title &&
              <h3>{ title }</h3>
            }
            { questionsComponents }
            <Buttons
              completed={completed}
              canEdit={canEdit}
              editing={editing}
              index={index}
              onSubmit={this.handleSubmit}
              onToggleEdit={this.handleToggleEdit}
              readOnly={readOnly}
              selected={selected}
              valid={valid}
              waiting={waiting}
            />
          </div>
        </div>
      </div>
    );
  }

}
