import React, { useEffect, useRef, useState }  from 'react';
import { getLocale } from '../utils';
import Portal from '../../utils/Portal';

const STATE_STOP = 'STATE_STOP';
const STATE_PLAYING = 'STATE_PLAYING';
const STATE_ENDED = 'STATE_ENDED';

function QuestionType16({ answer, id, card, properties: { videoURL }, onChange=noop, selectCard }) {
  const [videoState, setVideoState] = useState(STATE_STOP);
  const [videoReady, setVideoReady] = useState(false);
  const [hidden, setHidden] = useState(false);
  const videoElement = useRef(null);

  useEffect(() => {
    window.addEventListener('scroll', () => {
      setHidden(videoState==STATE_PLAYING && videoElement.current.getBoundingClientRect().bottom < videoElement.current.offsetHeight / 2);
    });
  });

  const handleVideoEnded = () => {
    setVideoState(STATE_ENDED);
    onChange(true);
  };

  const handleVideoCanPlay = () => {
    setVideoReady(true);
  };

  const handleVideoPlay = () => {
    setVideoState(STATE_PLAYING);
  };

  const handleVideoPause = () => {
    setVideoState(STATE_STOP);
  };

  const handlePlay = () => {
    videoElement.current.play();
  };

  const handleBackToVideo = () => {
    selectCard(card);
    window.CEPA.scrollToElement(videoElement.current);
  };

  const overlayContent = {
    [STATE_STOP]:
      videoReady ? (
        <div className="full-content clickable" onClick={handlePlay}>
          <div className="centered-content">
            <div className="icon-play-container" />
            <h3>{getLocale('start')}</h3>
          </div>
        </div>
      ) : null,
    [STATE_ENDED]: (
      <div className="full-content clickable" onClick={handlePlay}>
        <div className="centered-content">
          <div className="icon-play-container" />
          <h3>{getLocale('restart')}</h3>
        </div>
      </div>
    ),
  }[videoState];

  const overlay = overlayContent ? (
    <div className="video-player-overlay">{ overlayContent }</div>
  ) : null;

  return (
    <div className={`video-player${hidden ? ' hidden' : ''}`}>
      <video
        controls="controls"
        onCanPlay={handleVideoCanPlay}
        onPause={handleVideoPause}
        onPlay={handleVideoPlay}
        onEnded={handleVideoEnded}
        ref={videoElement}
        src={videoURL}
      />
      { overlay }
      <Portal id="back-to-video-portal">
        <div className={`back-to-video-wrapper${hidden ? ' video-player-hidden' : ''}`}>
          <button
            className="back-to-video"
            onClick={handleBackToVideo}
          >
            {getLocale('backToVideo')}
            <i className="icon-arrows-green" />
          </button>
        </div>
      </Portal>
    </div>
  );
}

QuestionType16.dontEdit = true;

QuestionType16.getValidValueForTest = (data) => {
  return true;
};

QuestionType16.parseProperties = ($div) => {
  const videoURL = $div.data('video-url') || `http://testing.cepadc.com/flash/assessment/videos/video_${$div.data('video-id')}.mp4`;
  return {
    videoURL,
  };
};

QuestionType16.nextIfValid = true;

export default QuestionType16;
