import React from 'react';
import QuestionType2 from './QuestionType2';


function QuestionType12(props) {
  return (
    <QuestionType2
      {...props}
    />
  );
}

QuestionType12.getValidValueForTest = QuestionType2.getValidValueForTest;

QuestionType12.parseValue = QuestionType2.parseValue;

QuestionType12.isValid = (answer) => {
  const years = new Date(new Date() - new Date(answer)).getFullYear() - 1970;
  return years >= 18;
};


export default QuestionType12;
