import { genericIsValid, getQuestionTypeComponent } from '../components/Question';
import * as selectors from '../state/selectors';


const $ = window.$;

export function getDataFromHTML($assessment) {
  const $fieldsets = $('> fieldset', $assessment);
  const cards = [];
  $fieldsets.each((cardIndex, fieldsetFor) => {
    const $questions = $('> div[data-type]', fieldsetFor);
    const questions = [];
    $questions.each((questionIndex, divFor) => {
      const $divFor = $(divFor);
      const type = $divFor.data('type');
      const QuestionTypeComponent = getQuestionTypeComponent(type);
      const answer = QuestionTypeComponent.parseValue($divFor);
      questions.push({
        answer,
        card: cardIndex,
        id: QuestionTypeComponent.parseID($divFor),
        index: questionIndex,
        completed: QuestionTypeComponent.parseCompleted($divFor),
        properties: QuestionTypeComponent.parseProperties($divFor),
        title: QuestionTypeComponent.parseTitle($divFor),
        type,
        valid: QuestionTypeComponent.isValid(answer),
      });
    });
    cards.push({
      index: cardIndex,
      className: fieldsetFor.className,
      completed: questions.every(({ completed }) => completed),
      valid: questions.every(({ valid }) => valid),
      waiting: false,
      questions
    });
  });

  const initialState = {
    cards,
    currentCardIndex: 0,
  };

  return initialState;
}


export function prepareInitialState(initialState) {
  if (initialState.finalText === undefined) {
    initialState.finalText = window.CEPA ? window.CEPA.finalText : '';
  };
  const pendingCards = selectors.getPendingCards(initialState);
  if (pendingCards.length) {
    initialState.currentCardIndex = pendingCards[0].index;
  }
  return initialState;
}

