import React, { Fragment, useRef } from 'react';
import { getLocale } from '../utils';
import { getIsoDateString } from '../utils';
import QuestionType2 from './QuestionType2';


function QuestionType7(props) {
  const { answer, htmlId, onChange, disabled } = props;

  const inputEl = useRef(null);
  const checkboxEl = useRef(null);

  let date = '';
  let never = false;
  if (answer==='0') {
    never = true;
  } else if (answer) {
    date = answer;
  }

  function handleChange(event) {
    if (checkboxEl.current.checked) {
      onChange('0');
    } else {
      onChange(getIsoDateString(inputEl.current.value));
    }
  }

  const checkboxHtmlId = `checkbox-${htmlId}`;
  return (
    <Fragment>
      <input
        type="date"
        id={htmlId}
        className="form-control"
        readOnly={disabled}
        ref={inputEl}
        name={htmlId}
        value={date}
        onChange={handleChange}
      />
      <div className="cepa-choice custom-control custom-checkbox">
        <input
          className="custom-control-input"
          type='checkbox'
          id={checkboxHtmlId}
          disabled={disabled}
          checked={never}
          onChange={handleChange}
          ref={checkboxEl}
        />
        <label
          className="custom-control-label"
          htmlFor={checkboxHtmlId}
          disabled={disabled}
        >
          {getLocale('never')}
        </label>
      </div>
    </Fragment>
  );
}

QuestionType7.getValidValueForTest = QuestionType2.getValidValueForTest;

export default QuestionType7;
