import React  from 'react';
import { noop } from '../utils';


function QuestionType4({ answer, disabled, properties: { options }, htmlId, index, onChange=noop }) {
  const content = options.map(({ id, label }, index) => (
    <option
      key={index}
      value={id}
    >
      { label }
    </option>
  ));
  return (
    <select
      id={htmlId}
      disabled={disabled}
      value={answer}
      className="custom-select"
      onChange={(event) => onChange(event.currentTarget.value)}
    >
      { content }
    </select>
  );
}

const parseOptions = ($div) => {
  const options = [];
  $('option', $div).each((index, optionFor) => {
    const $optionFor = $(optionFor);
    options.push({
      id: $optionFor.attr('value'),
      label: $optionFor.text(),
    });
  });
  return options;
};

QuestionType4.getValidValueForTest = ({ properties: { options } }) => {
  return options[1].id;
};

QuestionType4.parseProperties = ($div) => {
  return {
    options: parseOptions($div),
  };
};

QuestionType4.nextIfValid = true;

export default QuestionType4;
