import { combineReducers } from 'redux';
import * as types from './types';

/* State shape
{
  cards: array,
  currentCardIndex: number,
}
*/

const initialState = {
  cards: [],
  currentCardIndex: 0,
  finalText: null,
};


const questionReducer = ( state = null, action = {} ) => {
  switch (action.type) {
    case types.UPDATE_QUESTION:
      if (state.id === action.payload.id) {
        return {
          ...state,
          ...action.payload,
        };
      }else{
        return state;
      }
    default:
      return state;
  }
};


const cardReducer = ( state = null, action = {} ) => {
  switch (action.type) {
    case types.UPDATE_CARD:
      if (state.index === action.payload.index) {
        return {
          ...state,
          ...action.payload,
        };
      }else{
        return state;
      }
    case types.UPDATE_QUESTION:
      const questions = state.questions.map(questionFor => questionReducer(questionFor, action));
      return {
        ...state,
        questions,
        valid: questions.every(({ valid }) => valid),
        completed: questions.every(({ completed }) => completed),
      };
    default:
      return state;
  }
};


const cards = ( state = initialState.cards, action = {} ) => {
  switch (action.type) {
    case types.UPDATE_CARD:
    case types.UPDATE_QUESTION:
      return state.map(card => cardReducer(card, action));
    default:
      return state;
  }
};


const currentCardIndex = ( state = initialState.currentCardIndex, action = {} ) => {
  switch (action.type) {
    case types.SELECT_CARD:
      return action.payload.index;
    case types.DESELECT_CARD:
      return null;
    default:
      return state;
  }
};

const finalText = ( state = initialState.finalText, action = {} ) => {
  switch (action.type) {
    case types.SET_FINAL_TEXT:
      return action.payload;
    default:
      return state;
  }
};

const cardsReducer = combineReducers({
  cards,
  currentCardIndex,
  finalText,
});

export default cardsReducer;
