import React, { Component, Fragment }  from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import * as selectors from '../state/selectors';
import { noop, sameArray } from './utils';
import Card from './Card';
import Modal from './Modal';

const $ = window.$;


export function Controls({ currentCardIndex, totalCards }) {
  const reactChild = (
    <div className="cepa-controls">
      {/*<button className="btn btn-sm btn-outline-primary controls__prev-btn" onClick={onPrev} disabled={current===0}>Prev</button>*/}
      <span className="controls__numbers">{currentCardIndex+1} / {totalCards}</span>
      {/*<button className="btn btn-sm btn-outline-primary controls__next-btn" onClick={onNext} disabled={current===total-1}>Next</button>*/}
    </div>
  );
  const container = document.querySelector('.cepa-controls-wrapper');
  return container && ReactDOM.createPortal( reactChild, container );
}

export function Version({ version }) {
  const reactChild = (
    <div className="copyright">v{version}</div>
  );
  const container = document.querySelector('.brand');
  return container && ReactDOM.createPortal( reactChild, container );
}


export default @connect(
  (state, props) => ({
    cards: state.cards,
    totalCards: selectors.getTotalCards(state),
    currentCardIndex: selectors.getCurrentCardIndex(state),
    finalText: state.finalText,
  })
)
class Deck extends Component {

  state = {
    completed: false
  }

  constructor(props) {
    super(props);
    $('nav.navbar .navbar-brand').before('<div class="cepa-controls-wrapper"></div>');
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const { cards } = nextProps;
    const completed = cards.every(({ completed }) => completed);
    if (completed === prevState.completed) return {};
    return { completed };
  }

  componentDidUpdate(prevProps, prevState) {
    const { finalText } = this.props;
    const { completed } = this.state;
    if (completed && !finalText) {
      this.redirectIfNeeded();
    }
  }

  redirectIfNeeded = () => {
    if (window.CEPA.successRedirect) {
      window.location.href = window.CEPA.successRedirect;
    }
  }

  handleHide = () => {
    const { completed } = this.state;
    if (completed) {
      this.redirectIfNeeded();
    }
  }

  render() {
    const { cards, currentCardIndex, finalText, totalCards } = this.props;
    const { completed } = this.state;

    const cardsComponents = cards.map((data, index) => (
      <Card
        key={index}
        selected={currentCardIndex===index}
        {...data}
      />
    ));

    return (
      <Fragment>
        { Boolean(finalText) && completed && <Modal body={finalText} onHide={this.handleHide} /> }
        <div className="cepa-deck">
          <div className="cepa-deck__cards container">
            { cardsComponents }
          </div>
          <Controls
            currentCardIndex={currentCardIndex}
            totalCards={totalCards}
          />
          <Version
            version={process.env.REACT_APP_VERSION}
          />
        </div>
      </Fragment>
    );
  }

}
