import * as actions from './actions';
import * as selectors from './selectors';
import { getQuestionTypeComponent } from '../components/Question';


export const getClampIndex = ( index, getState ) => {
  const totalCards = selectors.getTotalCards(getState());
  const clampIndex = Math.max(0, Math.min(totalCards-1, index));
  return clampIndex;
};

export const selectCard = ( index ) => ( dispatch, getState ) => {
  const currentCardIndex = selectors.getCurrentCardIndex(getState());
  const clampIndex = getClampIndex(index, getState);
  if (clampIndex !== currentCardIndex) dispatch(actions.selectCard(clampIndex));
};

export const deltaCard = ( quantity ) => ( dispatch, getState ) => {
  const currentCardIndex = selectors.getCurrentCardIndex(getState());
  const clampIndex = getClampIndex(currentCardIndex + quantity, getState);
  if (clampIndex !== currentCardIndex) dispatch(actions.selectCard(clampIndex));
};

export const updateQuestionCallback = ( data ) => ( dispatch, getState ) => {
  dispatch(actions.updateQuestion(data));
  return Promise.resolve();
};

export const answerCurrentCardForTest = () => ( dispatch, getState ) => {
  const card = selectors.getCurrentCard(getState());
  card.questions.forEach((questionDataFor) => {
    const questionTypeComponent = getQuestionTypeComponent(questionDataFor.type);
    const validValueForTest = questionTypeComponent.getValidValueForTest(questionDataFor);
    dispatch(actions.updateQuestion({
      id: questionDataFor.id,
      answer: validValueForTest,
      valid: true,
    }));
  });
};

export const prevCard = () => deltaCard(-1);

export const nextCard = () => deltaCard(1);

export const gotoFirstUncompletedCard = (excludeIndex) => ( dispatch, getState ) => {

  let pendingCards = selectors.getPendingCards(getState(), true);
  pendingCards = pendingCards.filter(({index}) => index !== excludeIndex);
  if (pendingCards.length) {
    dispatch(actions.selectCard(pendingCards[0].index));
  }
};

export const deselectCard = actions.deselectCard;
export const updateCard = actions.updateCard;
export const updateQuestion = actions.updateQuestion;
export const setFinalText = actions.setFinalText;
