import React, { Fragment, useRef, useEffect } from 'react';


function QuestionType11({ answer, disabled, htmlId, index, onChange=noop, selected }) {
  const inputEl = useRef(null);

  useEffect(() => {
    if (index===0 && selected && !disabled)
    inputEl.current.focus();
  }, [disabled, index, selected]);

  return (
    <Fragment>
      <textarea
        id={htmlId}
        className="form-control"
        readOnly={disabled}
        ref={inputEl}
        name={htmlId}
        value={answer}
        onChange={(event) => onChange(event.currentTarget.value)}
      >{answer}</textarea>
    </Fragment>
  );
}

QuestionType11.getValidValueForTest = (data) => {
  return 'Test long text lorem ipsum.\nAnother line.';
};

export default QuestionType11;
