import React, { Fragment }  from 'react';
import { noop } from '../utils';
import Choice from './Choice';


function QuestionType3(props) {

  const content = props.properties.choices.map((choiceDataFor, index) => (
    <Choice
      {...props}
      key={index}
      data={choiceDataFor}
      onChange={(event) => props.onChange(event.currentTarget.value)}
    />
  ));
  return (
    <Fragment>
      { content }
    </Fragment>
  );
}

const parseChoices = ($div) => {
  const choices = [];
  $('input[type="radio"]', $div).each((index, inputFor) => {
    const $inputFor = $(inputFor);
    choices.push({
      id: $inputFor.attr('value'),
      label: $inputFor.parent().text(),
    });
  });
  return choices;
};

QuestionType3.getValidValueForTest = ({ properties: { choices } }) => {
  return choices[0].id;
};

QuestionType3.parseProperties = ($div) => {
  return {
    choices: parseChoices($div),
  };
};

QuestionType3.nextIfValid = true;

export default QuestionType3;
