import React, { Fragment }  from 'react';
import { noop } from '../utils';
import Choice from './Choice';


function QuestionType8(props) {

  function alterArrayItem(array, item, direction) {
    if (direction) {
      return [...array, item];
    } else {
      return array.filter(itemFor => itemFor !== item);
    }
  }

  function handleChange(event) {
    const checkbox = event.currentTarget;
    const newAnswer = alterArrayItem(props.answer, checkbox.value, checkbox.checked);
    props.onChange(newAnswer);
  }

  const content = props.properties.choices.map((choiceDataFor, index) => (
    <Choice
      {...props}
      key={index}
      data={choiceDataFor}
      choiceType="checkbox"
      onChange={handleChange}
    />
  ));
  return (
    <Fragment>
      { content }
    </Fragment>
  );
}

const parseChoices = ($div) => {
  const choices = [];
  $('input[type="checkbox"]', $div).each((index, inputFor) => {
    const $inputFor = $(inputFor);
    choices.push({
      id: $inputFor.attr('value'),
      label: $inputFor.parent().text(),
    });
  });
  return choices;
};

QuestionType8.getValidValueForTest = ({ properties: { choices } }) => {
  return [choices[0].id,];
};

QuestionType8.parseProperties = ($div) => {
  return {
    choices: parseChoices($div),
  };
};

export default QuestionType8;
